form {
    border: 5px solid #f1f1f1;
    margin-top: 20px;
    padding: 5px;
}

#signUpForm input[type=text], input[type=password] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    box-sizing: border-box;
}
#signUpForm button:hover {
  opacity: 0.8;
}
#signUpForm button {
background-color: #5CDA93;
color: white;
padding: 14px 20px;
margin: 8px 0;
border: none;
cursor: pointer;
width: 100%;
}
#signUpForm button:hover {
    opacity: 0.8;
}

#NameBlock{
    display: flex;
}
#signupError{
    color: red;
}
#successSignUp{
    color: slateblue;
   
    
}
#hidePass{
    width: 30px;
    height: 20px;
}
#showPass{
    width: 30px;
    height: 20px;
    color: #B6073B;
}
#passField{
    display: flex;
    align-items: center;
}